<template lang="html">
  <select class="cp-form_input" @change="handleChange">
    <option v-for="o in options" :value="o.v" :selected="isSelected(o.v)">{{ o.k }}</option>
  </select>
</template>

<script>
export default {
  props: {
    options: Array,
    value: String
  },
  methods: {
    handleChange (e) {
      this.$emit('input', e.target.value)
    },
    isSelected (v) {
      return v == this.value ? 'selected' : ''
    }
  }
}
</script>

<style lang="css">
</style>



