<template>
  <div id="app">
    <textarea class="cp-console">{{ console }}</textarea>
		<div class="cp-form">
      <form-item :label="item.label" :name="item.name" :key="item.name" :tip="item.tip" v-for="item in formItems">
        <CpInput v-if="item.type == 'input'" v-model="params[item.name]" />
        <CpSelect v-if="item.type == 'select'" :options="item.options" v-model="params[item.name]" />
      </form-item>

      <!-- <p class="cp-desc">
        埋点相关：<a href="http://appff.meizu.com/front/wiki/#!specifications/track.md" target="_blank">埋点公共参数文档</a>
        <a class="cp-btn" @click="addParam">添加参数</a>
      </p> -->

      <!-- <div class="cp-form_item" v-for="(item, idx) in plusParams">
        <input type="text" :value="item.key" class="cp-input_pluskey" @input="item.key = $event.target.value" />
        <input type="text" :value="item.value" class="cp-input_plusvalue" @input="item.value = $event.target.value"  />
        <a class="cp-btn warn" @click="removeParam(idx)">删除</a>
      </div> -->
		</div>
    <canvas id="qrcode" width="500" height="500"></canvas>
  </div>
</template>

<script>
import base64url from 'base64url'
import hexRgb from 'hex-rgb'
import FormItem from '@/components/FormItem'
import CpInput from '@/components/CpInput'
import CpSelect from '@/components/CpSelect'
import QRCode from 'qrcode'
export default {
  name: 'app',
  data () {
    return {
      formItems: [
        {
          type: 'select',
          label: '应用平台',
          name: 'turnTo',
          options: [
            { k: '浏览器', v: 'b' },
            { k: '应用商店', v: 'a' },
            { k: '主题美化', v: 't' }
          ],
          default: 'a'
        },
        {
          type: 'select',
          label: '运行方式',
          name: 'run',
          options: [
            { k: '停留中转页', v: '' },
            { k: '自动打开应用', v: '1' },
            { k: '延迟1秒打开（仅在应用打开较慢时使用）', v: '2' }
          ]
        },
        {
          type: 'select',
          label: '安装完成打开方式',
          name: 'openType',
          options: [
            { k: '手动打开', v: '0' },
            { k: '直接打开', v: '1' }
          ],
          default: '0'
        },
        { type: 'input', label: '包名', name: 'packageName' },
        { type: 'input', label: '应用版本code', name: 'versionCode' },
        { type: 'input', label: '应用ID', name: 'appId' },
        { type: 'input', label: '中转页url', name: 'gameUrl', tip: 'cp自定义页面，不填则默认使用简单页面' },
        { type: 'input', label: 'dplink', name: 'scheme', tip: 'cp提供dplink，不填默认打开应用首页' },
        { type: 'input', label: '页面标题', name: 'title' },
        { type: 'input', label: '按钮文案（安装）', name: 'installText' },
        { type: 'input', label: '按钮文案（更新）', name: 'updateText' },
        { type: 'input', label: '按钮文案（安装完成）', name: 'installCompleteText' },
        { type: 'input', label: '按钮文案（重试）', name: 'failureText', tip: '安装失败时的按钮文案' },
        { type: 'input', label: '页面背景颜色', name: 'backgroundColor', tip: '颜色值如 #F2F2F2，或rgb(225,225,225)' },
        { type: 'input', label: '按钮背景景颜色', name: 'btnColor', tip: '颜色值如 #F2F2F2，或rgb(225,225,225)' },
        { type: 'input', label: '按钮文字颜色', name: 'textColor', tip: '颜色值如 #F2F2F2，或rgb(225,225,225)' },
        { type: 'input', label: '埋点（页面来源）', name: 'business', tip: '<a target="_blank" href="http://appff.meizu.com/front/wiki/#!specifications/track.md#flyme_business_source">参考埋点文档(flyme_business_source字段)</a>'}
      ],
      params: {},
      plusParams: []
    }
  },
  computed: {
    console () {
      let result = 'https://i3.mzres.com/resources/appStore/cp/p-native.html?'
      let params = []
      let gameUrl = this.params.gameUrl && base64url.encode(this.params.gameUrl)
      let scheme = this.params.scheme && base64url.encode(this.params.scheme)
      for (let k in this.params) {
        if (this.params.hasOwnProperty(k) && ['scheme', 'gameUrl'].indexOf(k) == -1) {
          let value = this.params[k]
          if (~k.indexOf('Color') && ~value.indexOf('#')) {
            value = `rgb(${hexRgb(value).join(',')})`
          }
          if (value) {
            params.push(`${k}=${value}`)
          }
        }
      }
      if (gameUrl) {
        params.push('gameUrl=' + gameUrl)
      }
      if (scheme) {
        params.push('scheme=' + scheme)
      }
      if (this.plusParams.length > 0) {
        this.plusParams.forEach(p => {
          if (p.key && p.value) {
            params.push(`${p.key}=${p.value}`)
          }
        })
      }
      result += params.join('&')
      return result
    }
  },
  watch: {
    console(val) {
      var canvas = document.getElementById('qrcode')
      QRCode.toCanvas(canvas, val, function (error) {
        if (error) console.error(error)
        console.log('success!');
      })
    }
  },
  methods: {
    addParam () {
      this.plusParams.push({
        key: '',
        value: ''
      })
    },
    removeParam(e) {
      this.plusParams.splice(e, 1)
    }
  },
  created () {
    this.formItems.map(item => {
      this.$set(this.params, item.name, item.default || '')
    })
  },
  components: {
    FormItem, CpInput, CpSelect
  }
}
</script>

<style>
input,select,textarea {
  outline: none;
}
a {
  cursor: pointer;
}
#app {
  margin: 10px auto;
  width: 960px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 18px;
}
.cp-console {
  background: #888;
  color: #fff;
  padding: 10px;
  font-size: 16px;
  word-break: break-all;
  margin-bottom: 20px;
  padding: 10px;
  width: 100%;
}
.cp-desc {
  font-weight: bold;
  font-style: italic;
  color: #555;
}
.cp-btn {
  font-size: 16px;
  font-style: normal;
  padding: 2px 10px;
  border-radius: 3px;
  color: #fff;
  background-color: #42b983;
  float: right;
}
.cp-btn.warn {
  background-color: #f60;
}
.cp-input_pluskey {
  width: 200px !important;
}
</style>



